
.container.show-aside {
    position: relative;
    margin: 1rem auto;
}
.show-aside  .main{
    float: left;
}
.show-aside  .aside{
    float: left;
    padding-left: 1rem;
}

.show-aside .target {
    width: 100%;
    margin-bottom: 1rem;
    line-height: 2rem;
    background-color: #e5f8ff;
    padding-left: 2rem;
    border-radius: 4px;
    border: 1px solid #1e90ff;
}



@media (min-width: 1440px){
    .container {
        max-width: 1232px;
    }
    .show-aside {
        max-width: 1232px;
    }
    .show-aside .main {
        width: 75%;
    }

    .show-aside .aside {
        width: 25%;
    }
}

@media (max-width: 1439px) and (min-width: 768px){
    .container.show-aside .main {
        width: 70%;
    }
    .container.show-aside .aside {
        width: 30%;
    }
}

@media (max-width: 767px){
    .container.show-aside .main{
        width: 100%;
    }
    .container.show-aside .aside{
        display: none;
    }
}