.main-div {
    padding: 1.5rem;
    background-color: #fff;
    border-radius: 5px;
}
.info-card{
    padding: 0;
}
.info-card .info-card-header {
    height: 80px;
    border-radius: 5px 5px 0 0;
    margin-bottom: 60px;
}
.info-card .info-card-header .info-avatar{
    display: block;
    width: 120px;
    height: 120px;
    margin: 0 auto;
    border: 3px solid #fff;
    border-radius: 50%;
    -webkit-box-shadow: 0 0 2px rgba(0,0,0,.2);
    box-shadow: 0 0 2px rgba(0,0,0,.2);
    -webkit-transform: translateY(20px);
    transform: translateY(20px);
}

.info-card .info-card-body{
    cursor: default;
    padding-top: .3rem;
    border-bottom: 1px solid #eaecef;
    text-align: center;
}

.info-card .info-card-body .info-nickname{
    display: block;
    font-size: 1.5rem;
    font-weight: 700;
    text-align: center;
}

.info-card .info-card-body .info-desc {
    margin: .5rem 0;
    font-size: .7rem;
    padding: 1rem;
    color: #666;
}

.info-card .info-card-footer {
    text-align: center;
    padding: 1rem;
}

.info-card .qq-info {
    border-top: 1px solid #eaecef;
    border-bottom: 1px solid #eaecef;
    padding: .8rem;
    font-size: .8rem;
}
.info-card .qq-info .info-list-item {
    display: inline-block;
    width: 100%;
    max-width: 100%;
    overflow: hidden;
    text-overflow: ellipsis;
    vertical-align: top;
    white-space: nowrap;
    font-size: 14px;
    line-height: 1.5;
}
.info-card .qq-info .info-list-item a:hover{
    text-decoration: underline;
}

.info-card .qq-info .octicon{
    color: #6a737d;
    margin-top: 3px;
    margin-right: 5px;
    text-align: center;
    width: 16px;
    vertical-align: text-bottom;
    display: inline-block;
    fill: currentColor;
}