.header {
    background-color: #fff;
    -webkit-transition: all .5s ease-in-out;
    transition: all .5s ease-in-out;
}
body{
    position: relative;
}
.box{
    position: absolute;
    left: 0;
    top: 0;
    width: 9.6rem;
    height: 9.6rem;
    overflow: hidden;
    z-index: -2;
}
.item{
    position: relative;
    float: left;
    overflow: hidden;
}
.item span{
    position: absolute;
    width: 0;
    height: 0;
    border: 1.2rem solid transparent;
}
.item span:nth-of-type(1){
    left: -1.2rem;
    top: 0;
    border-top: 1.2rem solid #f00;
}
.item span:nth-of-type(2){
    left: 1.2rem;
    top: 0;
    border-top: 1.2rem solid hotpink;
}
.item span:nth-of-type(3){
    left: 0;
    top: -1.2rem;
    border-bottom: 1.2rem solid green;
}
.item span:nth-of-type(4){
    left: 0;
    top: 1.2rem;
    border-top: 1.2rem solid green;
}
.item span:nth-of-type(5){
    left: 0;
    top: 1.2rem;
    border-left: 1.2rem solid #f00;
}
.item span:nth-of-type(6){
    left: 0;
    top: 1.2rem;
    border-right: 1.2rem solid #000;
}
/* .header{
    background-image:  url('data:image/svg+xml;base64,');
  } */
.navbar{
    z-index: 20;
    top: 0;
    left: 0;
    right: 0;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    line-height: 2.2rem;
    padding: .7rem 1.4rem;
    border-bottom: 1px solid #eaecef;
    color: #2c3e50;
    background-color: hsla(0,0%,100%,.8);
    -webkit-transition: all .5s ease-out;
    transition: all .5s ease-out;
    overflow: hidden;
    position: relative;
    height: 3.6rem;
}
.navbar.fixed {
    position: fixed;
    background-color: #fff;
}
.navbar .navbar-site-name {
    position: relative;
    font-size: 1.2rem;
    font-weight: 600;
    color: #1e90ff;
}
.navbar .secondTitle {
    color: #666;
    font-size: .8rem;
}

.navbar .navbar-toggler{
    float: right;
    cursor: pointer;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    width: 2.2rem;
    border-radius: 50%;
    border: 2px solid transparent;
    text-align: center;
    z-index: 100;
}

.navbar .navbar-link{ 
    color: #2c3e50;
    border-bottom: 2px solid transparent;
}
.navbar .navbar-link.router-link-active, .navbar .navbar-link:hover {
    border-bottom: 2px solid #1e90ff;
}
.navbar .navbar-toggler .icon {
    fill: #1e90ff;
}

.banner {
    cursor: default;
    height: 6rem;
    border-bottom: 1px solid #eaecef;
    color: #333;
}
.banner .container {
    height: 100%;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
}
.banner .container .center {
    z-index: 19;
    text-align: center;
    -webkit-transition: all .2s ease-in-out;
    transition: all .2s ease-in-out;
}

.banner .container .center:hover {
    -webkit-transform: scale(1.2);
    transform: scale(1.2);
}

.fade-enter, .fade-appear {
    opacity: 0;
    transform: translateY(40px);
}
.fade-enter-active, .fade-appear-active { 
    opacity: 1;
    transform: translateY(0);
    transition: all 0.4s ease-in;
}
.fade-enter-done {
    transform: translateY(0);
    opacity: 1;
}
.fade-exit {
    transform: translateY(0);
    opacity: 1;
}
.fade-exit-active {
    opacity: 0;
    transform: translateY(40px);
    transition: all 0.4s ease-in;
}
.fade-exit-done {
    opacity: 0;
    transform: translateY(0);
}

.progress-enter, .progress-appear {
    position: fixed;
    left: 0;
    top: 0;
    width: 0;
    height: 2px;
    background: #1e90ff;
    z-index: 99;
}
.progress-enter-active, .progress-appear-active { 
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 2px;
    background: #1e90ff;
    transition: width 0.35s ease-in;
    z-index: 99;
}
.progress-enter-done {
    position: fixed;
    left: 0;
    top: 0;
    width: 0;
    height: 2px;
    background: #1e90ff;
    z-index: 99;
}
.progress-exit {
    position: fixed;
    left: 0;
    top: 0;
    width: 0;
    height: 2px;
    background: #1e90ff;
    z-index: 99;
}
.progress-exit-active {
    position: fixed;
    left: 0;
    top: 0;
    width: 0;
    height: 2px;
    background: #1e90ff;
    transition: width 0.35s ease-in;
    z-index: 99;
}
.progress-exit-done {
    position: fixed;
    left: 0;
    top: 0;
    width: 0;
    height: 2px;
    background: #1e90ff;
    z-index: 99;
}

@media (min-width: 768px) {
    .navbar .navbar-links {
        float: right;
        margin: 0;
        padding: 0;
        line-height: 2.2rem;
        list-style: none;
    }
    .navbar .navbar-link {
        display: inline-block;
        height: 100%;
        padding: 0 .25rem;
        margin-left: 1rem;
        margin-bottom: -10px;
        color: #2c3e50;
        -webkit-transition: all .3s;
        transition: all .3s;
    }
    .navbar .navbar-toggler{
        display: none;
    }
}

@media (max-width: 767px){
    .navbar .navbar-toggler{
        display: inline;
    }
    .navbar .navbar-links {
        display: none;
        position: fixed;
        top: 3.6rem;
        right: 0;
        text-align: center;
        padding: .7rem 2rem;
        border: 1px solid #eaecef;
        border-top: none;
        background-color: hsla(0,0%,100%,.8);
        z-index: 100;
        -webkit-transition: all .5s ease-out;
        transition: all .5s ease-out;
    }
    .navbar .navbar-links.show{
        display: block;
    }
    .navbar .navbar-link {
        display: block;
    }
}

